export default {
  computed: {
    availableLocales() {
      return this.$i18n.locales.filter((i) => i.code !== this.$i18n.locale)
    },
  },
  components:{
    
  },

  data() {
    return {
      isOpen: false
    }
  },

  methods: {
    toggle() {
      this.isOpen = !this.isOpen
    },
    async changeLocale(locale) {
      await this.$i18n.setLocale(locale)
    }
  }
}
